import * as React from "react";
import { useStyles } from "./useStyles";
import {
  ListItem,
  List,
  Toolbar,
  AppBar,
  Button,
  Typography,
} from "@material-ui/core";
import { NavLink } from "react-router-dom";
// import theme from "../../../Theme/GlobalFontSizes";
// import { MuiThemeProvider } from "@material-ui/core/styles";
import { Hidden } from "@material-ui/core";
const Logo = require("../../assets/logo.png");
// import CustomButton from "../../../Components/CustomButton";

const Header = () => {
  const { logo, list, appbarsolid, link, root, active } = useStyles();

  return (
    <>
      <AppBar className={root} position="sticky">
        <Toolbar className={appbarsolid}>
          <section>
            <img className={logo} alt="logo" src={Logo} />
          </section>
          <Hidden smDown>
            <List className={list}>
              <ListItem>
                <Button className={list}>
                  <NavLink activeClassName={active} className={link} to="/home">
                    <Typography variant="caption">HOME</Typography>
                  </NavLink>
                </Button>
              </ListItem>
              <ListItem>
                <NavLink to="/" activeClassName={active} className={link}>
                  <Button>Post an Ad</Button>
                  {/* <CustomButton onClick={navigated}>
                    Request A Quote
                  </CustomButton> */}
                </NavLink>
              </ListItem>
            </List>
          </Hidden>
        </Toolbar>
      </AppBar>
    </>
  );
};

export default Header;
